function isProd() {
  return (
    process.env.NODE_ENV === 'prod' || process.env.NODE_ENV === 'production'
  )
}

function getCorsOptions(origin: string) {
  return {
    origin,
    optionsSuccessStatus: 200
  }
}

export default { getCorsOptions, isProd }



